@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("..fonts/opensans.woff") format "woff", url("..fonts/opensans.woff2") format "woff2";
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("..fonts/opensansbold.woff") format "woff", url("..fonts/opensansbold.woff2") format "woff2";
}
.about-us {
  min-width: 260px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .about-us {
    padding: 50px;
    min-width: 768px;
    padding-top: 59px;
    padding-bottom: 90px;
  }
}
@media (min-width: 1150px) {
  .about-us {
    padding: 0;
    padding-top: 68px;
    padding-bottom: 98px;
  }
}
.about-us__wrapper {
  padding: 30px;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1150px) {
  .about-us__wrapper {
    padding-left: 40px;
    padding-right: 40px;
  }
}
.about-us__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  align-items: center;
  margin: 0;
  margin-bottom: 28px;
}

@media (min-width: 768px) {
  .about-us__title {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 41px;
  }
}
@media (min-width: 1150px) {
  .about-us__title {
    font-size: 53px;
    line-height: 57px;
    margin-bottom: 39px;
  }
}
.about-us__list {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-rows: auto;
  row-gap: 32px;
}

@media (min-width: 768px) {
  .about-us__list {
    grid-template-columns: minmax(260px, 265px) minmax(320px, 325px);
    grid-template-rows: auto;
    column-gap: 70px;
    row-gap: 55px;
    font-size: 20px;
    line-height: 22px;
  }
}
.about-us__item {
  display: flex;
  flex-direction: column;
}

.about-us__item:nth-child(1) .about-us__item-icon {
  margin-bottom: 16px;
}

.about-us__item:nth-child(2) .about-us__item-icon {
  margin-bottom: 26px;
}

.about-us__item:nth-child(3) .about-us__item-icon {
  margin-bottom: 21px;
}

.about-us__item:nth-child(4) .about-us__item-icon {
  margin-bottom: 17px;
}

.about-us__item:nth-child(5) .about-us__item-icon {
  margin-bottom: 26px;
}

.about-us__item:nth-child(6) .about-us__item-icon {
  margin-bottom: 20px;
}

@media (min-width: 768px) {
  .about-us__item:nth-child(1),
.about-us__item:nth-child(2),
.about-us__item:nth-child(3) {
    grid-column: 1/2;
  }

  .about-us__item:nth-child(4),
.about-us__item:nth-child(5),
.about-us__item:nth-child(6) {
    grid-column: 2/3;
  }

  .about-us__item:nth-child(1),
.about-us__item:nth-child(4) {
    grid-row: 1/2;
  }

  .about-us__item:nth-child(2),
.about-us__item:nth-child(5) {
    grid-row: 2/3;
  }

  .about-us__item:nth-child(3),
.about-us__item:nth-child(6) {
    grid-row: 3/4;
  }
}
.additional-list {
  display: none;
}

.additional-list__item:hover {
  opacity: 0.6;
}

.additional-list__item:active {
  opacity: 0.3;
}

@media (min-width: 1150px) {
  .additional-list {
    display: flex;
    flex-wrap: wrap;
    font-weight: 700;
    font-size: 15px;
    line-height: 24px;
    margin: 0;
    margin-top: 19px;
    padding: 0;
    padding-left: 50px;
    order: 2;
    max-width: 600px;
  }

  .additional-list__item {
    margin-bottom: 19px;
  }

  .additional-list__link {
    color: black;
    text-decoration: none;
    padding-top: 19px;
    padding-bottom: 19px;
    padding-right: 55px;
  }
}
.button {
  display: block;
  height: 52px;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  border: 2px solid #444444;
  cursor: pointer;
  color: #444444;
  background: white;
  text-align: center;
  box-sizing: border-box;
}

.button:hover {
  color: white;
  background: #444444;
}

.button:active {
  color: white;
  background: #444444;
  opacity: 0.3;
}

.catalog-container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  background-color: #ffffff;
}

.catalog-filter {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.catalog-filter__fieldset {
  border: none;
  position: relative;
  margin: 0 30px 41px;
  max-width: 1050px;
  padding: 0;
}

.catalog-filter__divider {
  border-top: solid 1px #61d1bb;
}

.catalog-filter__title {
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
  color: #63d1bb;
  margin: 0 0 20px 0;
  padding: 0 22px 0 0;
  text-transform: uppercase;
  position: relative;
  display: inline-block;
  background-color: #ffffff;
  top: -10px;
}

.catalog-filter__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.catalog-filter__radio {
  padding-top: 2px;
  padding-bottom: 2px;
  min-height: 36px;
  align-items: center;
}

.catalog-filter__checkbox,
.catalog-filter__radio {
  padding-left: 61px;
  display: flex;
  position: relative;
}

.catalog-filter__wrapper--radio .catalog-filter__item:not(:last-child) {
  margin-bottom: 12px;
}

.catalog-filter__checkbox {
  padding-top: 10px;
  padding-bottom: 23px;
}

.catalog-filter__wrapper--checkbox .catalog-filter__title {
  margin-bottom: 14px;
}

.catalog-filter__wrapper--checkbox .catalog-filter__item:not(:last-child) {
  margin-bottom: 23px;
}

.catalog-filter__control-mark {
  position: absolute;
  width: 36px;
  height: 36px;
  border: 2px solid #444444;
  border-radius: 50%;
  left: 0;
  top: 0;
}

.catalog-filter__control-input[type=checkbox] + .catalog-filter__control-mark {
  border-radius: 0;
  width: 34px;
  height: 34px;
}

.catalog-filter__radio:hover .catalog-filter__control-mark {
  border-color: #61d1bb;
}

.catalog-filter__control-input:focus + .catalog-filter__control-mark {
  outline: 3px solid #83b3d3;
}

.catalog-filter__checkbox:hover .catalog-filter__control-mark {
  border-color: #61d1bb;
}

.catalog-filter__radio:active .catalog-filter__control-mark {
  border-color: rgba(0, 0, 0, 0.3);
}

.catalog-filter__checkbox:active .catalog-filter__control-mark {
  border-color: rgba(0, 0, 0, 0.3);
}

.catalog-filter__control-input[type=radio]:checked + .catalog-filter__control-mark::before {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 16px;
  height: 16px;
  background-color: #61d1bb;
  border-radius: 50%;
  content: "";
}

.catalog-filter__control-input[type=checkbox]:checked + .catalog-filter__control-mark::before {
  position: absolute;
  top: 14px;
  left: 9px;
  width: 2px;
  height: 10px;
  transform: rotate(-45deg);
  background-color: #000000;
  content: "";
}

.catalog-filter__control-input[type=checkbox]:checked + .catalog-filter__control-mark::after {
  position: absolute;
  top: 5px;
  left: 20px;
  width: 2px;
  height: 22px;
  transform: rotate(45deg);
  background-color: #000000;
  content: "";
}

.catalog-filter__control-label {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  display: block;
}

.catalog-filter__require {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #444444;
}

.catalog-filter__label {
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
}

.catalog-filter__button {
  line-height: 16px;
  color: #444444;
  padding: 17px;
  min-width: 260px;
  margin: 0 auto;
}

.catalog-filter__inner {
  display: grid;
  grid-template-columns: 1fr;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 55px;
  row-gap: 28px;
}

.catalog-filter__name,
.catalog-filter__surname,
.catalog-filter__patronymic,
.catalog-filter__phone-number,
.catalog-filter__email-text,
.catalog-filter__message-text-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 23px;
  position: relative;
}

.catalog-filter__patronymic,
.catalog-filter__email-text {
  margin-bottom: 0;
}

.catalog-filter__phone-number {
  margin-bottom: 4px;
}

.catalog-filter__message-text-wrapper {
  margin: 0;
}

.catalog-filter__wrapper--data .catalog-filter__title {
  margin-bottom: 0;
}

.catalog-filter__wrapper--message .catalog-filter__title {
  margin-bottom: 12px;
}

.catalog-filter__wrapper--message .catalog-filter__fieldset {
  margin-bottom: 16px;
}

.catalog-filter__name-input,
.catalog-filter__surname-input,
.catalog-filter__patronymic-input,
.catalog-filter__phone-number-input,
.catalog-filter__email-text-input,
.catalog-filter__message-text {
  border: none;
  font-weight: 400;
  font-size: 17px;
  line-height: 18px;
  padding: 0;
  font-family: inherit;
  padding-right: 5px;
  padding-top: 16px;
  padding-bottom: 16px;
  box-shadow: inset 0 -2px 0 #000000;
}

.catalog-filter__message-text {
  padding: 17px;
  border: 2px solid #444444;
  box-shadow: none;
  min-height: 106px;
}

.catalog-filter__phone-number-input,
.catalog-filter__email-text-input {
  padding-top: 10px;
  padding-bottom: 15px;
  padding-right: 40px;
}

.catalog-filter__name-input:hover,
.catalog-filter__surname-input:hover,
.catalog-filter__patronymic-input:hover,
.catalog-filter__phone-number-input:hover,
.catalog-filter__email-text-input:hover {
  box-shadow: inset 0 -2px 0 #61d1bb;
  padding-left: 10px;
  fill: #62d1ba;
}

.catalog-filter__message-text:hover {
  border-color: #62d1ba;
}

.catalog-filter__message-text:active {
  color: black;
}

.catalog-filter__name-input:active,
.catalog-filter__surname-input:active,
.catalog-filter__patronymic-input:active,
.catalog-filter__phone-number-input:active,
.catalog-filter__email-text-input:active {
  fill: #454545;
}

.catalog-filter__phone-icon,
.catalog-filter__email-text-icon {
  position: absolute;
  right: 8px;
  top: 6px;
  fill: black;
}

.catalog-filter__email-text-icon {
  right: 4px;
}

.catalog-filter__phone-number-input:hover + .catalog-filter__phone-icon,
.catalog-filter__email-text-input:hover + .catalog-filter__email-text-icon {
  fill: #62d1ba;
}

@media (min-width: 768px) {
  .catalog-filter__wrapper:nth-child(2n) {
    background-color: #f1f1f1;
  }

  .catalog-filter__fieldset {
    display: flex;
    flex-wrap: wrap;
    padding: 50px;
    margin: 0 auto;
  }

  .catalog-filter__wrapper--radio .catalog-filter__fieldset {
    flex-wrap: nowrap;
  }

  .catalog-filter__wrapper--radio .catalog-filter__item:not(:last-child) {
    margin-bottom: 24px;
  }

  .catalog-filter__wrapper--radio .catalog-filter__list {
    display: block;
  }

  .catalog-filter__wrapper--checkbox .catalog-filter__fieldset {
    flex-wrap: nowrap;
    padding-right: 20px;
    padding-bottom: 27px;
  }

  .catalog-filter__radio {
    padding-top: 16px;
    padding-bottom: 16px;
    min-height: 10px;
    padding-left: 78px;
  }

  .catalog-filter__divider {
    border: none;
    margin-top: 14px;
  }

  .catalog-filter__title {
    background-color: inherit;
    top: 0;
    margin: 0;
    padding: 0;
    max-width: 90px;
  }

  .catalog-filter__label {
    font-size: 19px;
    line-height: 18px;
  }

  .catalog-filter__control-mark {
    width: 47px;
    height: 47px;
  }

  .catalog-filter__control-input[type=radio]:checked + .catalog-filter__control-mark::before {
    top: 14px;
    left: 14px;
    width: 20px;
    height: 20px;
  }

  .catalog-filter__control-input[type=checkbox]:checked + .catalog-filter__control-mark::before {
    top: 22px;
    left: 14px;
    width: 2px;
    height: 16px;
  }

  .catalog-filter__control-input[type=checkbox]:checked + .catalog-filter__control-mark::after {
    top: 12px;
    left: 28px;
    width: 2px;
    height: 28px;
  }

  .catalog-filter__control-input[type=checkbox] + .catalog-filter__control-mark {
    width: 48px;
    height: 48px;
  }

  .catalog-filter__wrapper--checkbox .catalog-filter__item:not(:last-child) {
    margin-bottom: 24px;
    width: 262px;
  }

  .catalog-filter__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: 132px;
  }

  .catalog-filter__wrapper--checkbox .catalog-filter__list {
    margin-left: 122px;
    max-width: 700px;
  }

  .catalog-filter__chekbox {
    padding-top: 17px;
    padding-bottom: 17px;
    padding-left: 82px;
    padding-right: 20px;
  }

  .catalog-filter__control-label {
    font-size: 19px;
    line-height: 18px;
  }

  .catalog-filter__name {
    margin-left: 124px;
    flex-grow: 1;
    flex-direction: row;
    align-items: center;
    margin-bottom: 24px;
  }

  .catalog-filter__wrapper--data {
    padding: 50px;
  }

  .catalog-filter__wrapper--data .catalog-filter__fieldset {
    padding: 0;
  }

  .catalog-filter__name-input,
.catalog-filter__surname-input,
.catalog-filter__patronymic-input {
    flex-grow: 1;
    padding-left: 18px;
    padding-top: 13px;
    padding-bottom: 13px;
    margin-left: 24px;
    font-size: 19px;
    line-height: 18px;
  }

  .catalog-filter__name-input:hover,
.catalog-filter__surname-input:hover,
.catalog-filter__patronymic-input:hover {
    padding-left: 18px;
  }

  .catalog-filter__phone-number-input,
.catalog-filter__email-text-input {
    background-color: inherit;
    flex-grow: 1;
    font-size: 19px;
    line-height: 18px;
    padding: 11px 200px 15px 17px;
  }

  .catalog-filter__phone-number-input:hover,
.catalog-filter__email-text-input:hover {
    padding-left: 17px;
  }

  .catalog-filter__wrapper--message .catalog-filter__fieldset {
    margin-bottom: 4px;
  }

  .catalog-filter__message-text {
    background-color: inherit;
    flex-grow: 1;
    font-size: 19px;
    line-height: 18px;
    padding: 15px;
  }

  .catalog-filter__surname {
    margin-left: 167px;
    flex-grow: 1;
    flex-direction: row;
    margin-bottom: 24px;
    align-items: center;
  }

  .catalog-filter__patronymic {
    margin-left: 167px;
    flex-direction: row;
    flex-grow: 1;
