.catalog {
  position: relative;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
}

.catalog__inner {
  background-color: #f9f9f9;
}

.catalog__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  background-image: url("../img/background-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 50% -17px;
  max-width: 1150px;
}

@media (min-width: 768px) {
